import React from "react";
import { toast } from "react-toastify";

import Button from "react-bootstrap/Button";
import Card from "react-bootstrap/Card";
import Col from "react-bootstrap/Col";
import Form from "react-bootstrap/Form";
import Row from "react-bootstrap/Row";
import axios from "axios";
import { useParams, useNavigate } from "react-router-dom";
import { AppRoutes, ISocials } from "../../../types";

export interface ISocialProfileProps {}

export const SocialProfile: React.FC<ISocialProfileProps> = () => {
  const navigate = useNavigate();
  const { id } = useParams();
  console.log({ id });
  const [state, setState] = React.useState<ISocials>({
    _id: "",
    site: "",
    username: ""
  });

  React.useEffect(() => {
    let stillHere = true;

    const fetchData = async () => {
      const { data } = await axios.get(
        `http://localhost:9899/api/v1/socials/${id}`
      );

      if (stillHere) {
        setState(data);
      }
    };

    fetchData();

    return () => {
      stillHere = false;
    };
  }, []);

  const handleChange = (event: React.ChangeEvent<HTMLInputElement>): void => {
    setState({
      ...state,
      [event.target.name]: event.target.value
    });
  };

  const handleSubmit = async (e: any): Promise<void> => {
    e.preventDefault();
    if (!state.username || !state.site) {
      toast.error("Error, Site and username can not be empty");
      return;
    }

    try {
      const { data } = await axios.put(
        `http://localhost:9899/api/v1/socials/${state._id}`,
        {
          ...state
        }
      );

      if (data?.error || !data.success) {
        throw new Error();
      }

      toast.success("Booyah!");
    } catch (error) {
      toast.error("Error, Please try again later!");
    }
  };

  const handleDelete = async (
    e: React.MouseEvent<HTMLButtonElement, MouseEvent>
  ) => {
    e.preventDefault();

    try {
      const { data } = await axios.delete(
        `http://localhost:9899/api/v1/socials/${state._id}`
      );

      if (data?.error || !data.success) {
        throw new Error();
      }

      data?.success && navigate(AppRoutes.Socials);
    } catch (error) {
      toast.error("Error, Please try again later!");
      console.error(error);
    }
  };

  return (
    <Card className="mb-3">
      <Card.Body>
        <Card.Title>Social Network</Card.Title>
        <Form onSubmit={handleSubmit}>
          <Row>
            <Col>
              <Form.Group className="mb-3">
                <Form.Label>Site</Form.Label>
                <Form.Control
                  size="sm"
                  name="site"
                  onChange={handleChange}
                  type="text"
                  value={state.site}
                />
              </Form.Group>
            </Col>
            <Col> </Col>
          </Row>

          <Row>
            <Col>
              <Form.Group className="mb-3">
                <Form.Label>Username</Form.Label>
                <Form.Control
                  size="sm"
                  name="username"
                  onChange={handleChange}
                  type="text"
                  value={state.username}
                />
              </Form.Group>
            </Col>
            <Col> </Col>
          </Row>

          <Row>
            <Col>
              <Form.Group className="mb-3">
                <Button variant="primary" type="submit" size="sm">
                  Submit
                </Button>
              </Form.Group>
            </Col>
          </Row>

          <Row>
            <Col>
              <Form.Group className="mb-3">
                <Button
                  variant="danger"
                  type="button"
                  size="sm"
                  onClick={handleDelete}
                >
                  Delete
                </Button>
              </Form.Group>
            </Col>
          </Row>
        </Form>
      </Card.Body>
    </Card>
  );
};
